import React, { useState, useEffect } from "react";
import Sidebar from "../../components/Admin/Sidebar";
import TopBar from "../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import {
  Eye,
  Trash2,
  Edit,
  Lightbulb,
  Building2,
  UserCheck,
  Component,
  Presentation,
  BookText,
  ShieldOff,
} from "lucide-react";
import axios from "axios";
function AdminDashboard() {
  const [search, setSearch] = useState("");
  const [users, setrecord] = useState([]);

  const filteredUsers = users.filter(
    (user) =>
      user.first_name.toLowerCase().includes(search.toLowerCase()) ||
      user.email.toLowerCase().includes(search.toLowerCase()) ||
      user.last_name.toLowerCase().includes(search.toLowerCase())
  );

  const columns = [
    {
      name: "Name",
      selector: (row) => row.first_name + " " + row.last_name,
      sortable: true,
    },
    {
      name: "Email",
      selector: (row) => row.email,
      sortable: true,
    },
    {
      name: "Phone",
      selector: (row) => row.phone,
      sortable: true,
    },
    {
      name: "Registered Date",
      selector: (row) => new Date(row.created_at).toISOString().split("T")[0],
      sortable: true,
    },
  ];
  const [isOpen, setIsOpen] = useState(false);
  const [totalusers, settotalusers] = useState([]);
  const [totalCompany, settotalCompany] = useState([]);
  const [totalModule, settotalModule] = useState([]);
  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  document.title = "Admin Dashbord Page";
  const apiUrl = "https://blueprintcatalyst.com/api/api/admin/dashboard/";
  useEffect(() => {
    getTotalUsers();
  }, []);
  const getTotalUsers = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(
        apiUrl + "getTotalUsersDashboard",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json", // Ensure the content type is JSON
          },
        }
      );
      var respo = res.data.results;
      setrecord(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  useEffect(() => {
    getTotalCompany();
  }, []);
  const getTotalCompany = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getTotalCompany", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      settotalCompany(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  useEffect(() => {
    getTotalmodule();
  }, []);
  const getTotalmodule = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getTotalmodule", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      settotalModule(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  return (
    <div>
      <div className="d-flex align-items-start gap-0">
        <Sidebar />
        <div className="d-flex flex-column gap-0 w-100">
          <TopBar />
          <section className="dashboard_adminh">
            <div className="container-xl">
              <div className="row gy-4">
                <div className="col-12">
                  <h2>Dashboard</h2>
                </div>
                <div className="col-12">
                  <div className="row gy-4">
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <Lightbulb />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Total Entrepreneurs</h4>
                          <p>{totalusers.length}</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <Building2 />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Total Company</h4>
                          <p> {totalCompany.length}</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <UserCheck />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Subscribers</h4>
                          <p>1</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <Component />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Total Module</h4>
                          <p>{totalModule.length}</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <Presentation />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Active Metting</h4>
                          <p>1</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <ShieldOff />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Expired Metting</h4>
                          <p>1</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="d-flex align-items-center gap-2 dashmain_box">
                        <div className="icon_img">
                          <BookText />
                        </div>
                        <div className="d-flex flex-column gap-0">
                          <h4>Total Docs</h4>
                          <p>1</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <div className="col-12">
                  <div className="card p-3">
                    <h5 className="mb-3">Latest Users</h5>
                    <div className="d-flex justify-content-end align-items-end">
                      <div class="search-bar">
                        <input
                          type="text"
                          placeholder="Search by name or email..."
                          value={search}
                          onChange={(e) => setSearch(e.target.value)}
                        />
                        <span class="search-icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                          >
                            <path
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              stroke-width="2"
                              d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                            />
                          </svg>
                        </span>
                      </div>
                    </div>
                    <DataTable
                      columns={columns}
                      data={filteredUsers}
                      pagination
                      highlightOnHover
                      striped
                      responsive
                      noDataComponent={
                        <div className="text-center">
                          <span>No users found</span>
                        </div>
                      }
                    />
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default AdminDashboard;
